.list-item {
  margin-bottom: 3rem;
  position: relative;
  display: flex;
  flex-direction: column;
  margin-right: 70px;
}

.list-item:before {
  content: "";
  width: 10px;
  height: 10px;
  border-radius: 50%;
  position: absolute;
  left: 0;
  margin-left: -24px;
  margin-top: 12px;
  background: #c4c4c4;
  z-index: 99;
}

.info-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 8px;
  flex-wrap: wrap;
}

.year {
  background: rgb(34, 34, 34);
  color: #fff;
  padding: 4px 10px 6px 10px;
  border-radius: 10px;
  font-weight: 500;
  margin-right: 20px;
}

.dark-year {
  background: #fff;
  color: rgb(34, 34, 34);
}

.title {
  font-weight: 500;
  font-size: larger;
  padding-bottom: 6px;
  margin-right: 20px;
}

.duration {
  color: #929292;
  padding-bottom: 5px;
}

.description {
  font-size: large;
  color: rgb(120, 113, 108);
}

.dark-description {
  color: rgb(170, 162, 156);
}

@media (max-width: 506px) {
  .year {
    font-size: 11pt;
  }
  .title {
    font-size: large;
  }
}

@media (max-width: 481px) {
  .year {
    margin-bottom: 8px;
  }
  .title {
    font-size: large;
  }
}
